import * as layout from "../../routes/$layout.js";

const components = [
	() => import("../../routes/index.js"),
	() => import("../../routes/reklamace.js"),
	() => import("../../routes/checkout.js"),
	() => import("../../routes/kontakt.js"),
	() => import("../../routes/vraceni.js"),
	() => import("../../routes/obchod.js"),
	() => import("../../routes/about.js"),
	() => import("../../routes/boty/index.js"),
	() => import("../../routes/boty/[slug].js"),
	() => import("../../routes/gdpr.js")
];

export const pages = (d => [
	{
		// index.svelte
		pattern: /^\/$/,
		parts: [
			[components[0]]
		]
	},

	{
		// reklamace.svelte
		pattern: /^\/reklamace\/?$/,
		parts: [
			[components[1]]
		]
	},

	{
		// checkout.svelte
		pattern: /^\/checkout\/?$/,
		parts: [
			[components[2]]
		]
	},

	{
		// kontakt.svelte
		pattern: /^\/kontakt\/?$/,
		parts: [
			[components[3]]
		]
	},

	{
		// vraceni.svelte
		pattern: /^\/vraceni\/?$/,
		parts: [
			[components[4]]
		]
	},

	{
		// obchod.svelte
		pattern: /^\/obchod\/?$/,
		parts: [
			[components[5]]
		]
	},

	{
		// about.svelte
		pattern: /^\/about\/?$/,
		parts: [
			[components[6]]
		]
	},

	{
		// boty/index.svelte
		pattern: /^\/boty\/?$/,
		parts: [
			[components[7]]
		]
	},

	{
		// boty/[slug].svelte
		pattern: /^\/boty\/([^/]+?)\/?$/,
		parts: [
			[components[8], m => ({ slug: d(m[1]) })]
		]
	},

	{
		// gdpr.svelte
		pattern: /^\/gdpr\/?$/,
		parts: [
			[components[9]]
		]
	}
])(decodeURIComponent);

export const ignore = [
	
];

export { layout };